import {Injectable, Type} from '@angular/core';
import {PluginManagerModule} from './plugin-manager.module';
import {COMMON_DEPS} from './common-deps';
import {BehaviorSubject} from 'rxjs';

const SystemJs = (window as any).System;

export abstract class componentData {
  constructor(public configData: any) {}
}

export class PluginData extends componentData {
}

export function createPlugin(config: {
  name: string;
  module: Type<any>;
  component: Type<any>
}) {
  return new NgPlugin(config.name, config.module, config.component);
}

export class NgPlugin {
  constructor(
    public name: string,
    public module: Type<any>,
    public component: Type<any>) {
  }
}

@Injectable({
  providedIn: PluginManagerModule
})
export class PluginManagerService {

  public filterVa$ = new BehaviorSubject<any>(null);

  depsDefined = false;
  content: any;
  name: any;
  componentData: any;

  constructor() { }

  // æ¥æ¶ç»ä»¶ä¼ å¼
  compToMid(data) {
    this.filterVa$.next(data);
    console.log(data);
  }

  // ç»ç»ä»¶ä¼ å¼
  midToComp() {
    console.log(this.componentData);
    return this.componentData;
  }

  // æ¥æ¶ä¸»æ¡æ¶ä»£ç ä¼ å¼
  parentToMid(data) {
    this.componentData = data;
    console.log(data);
  }

  // ç»ä¸»æ¡æ¶ä¼ å¼
  midToParent() {
    console.log(2);
  }


  defineDeps() {
    if (this.depsDefined) {
      return;
    }
    Object.keys(COMMON_DEPS).forEach(dep =>
      (window as any).define(dep, [], () => COMMON_DEPS[ dep ])
    );
    this.depsDefined = true;
  }

  setData(data) {
    this.content = data;
  }

  getData() {
    return this.content;
  }

  set filterVal(name) {
    this.name = name;
    console.log(this.name);
  }

  get filterVal() {
    console.log(this.name);
    return this.name;
  }

  loadModule(name: string): Promise<NgPlugin> {
    this.defineDeps();
    return SystemJs.import(`./assets/${name}/${name}.umd.js`)
      .then(() => SystemJs.import(name))
      .then(plugin => {
        if (plugin instanceof NgPlugin) {
          return Promise.resolve(plugin);
        } else {
          throw new TypeError('This module is not a valid plugin');
        }
      });
  }
}
